body {
  margin: 1em 2em;
  font-family: sans-serif;
}

.has-flex {
  display: flex;
}

.is-flexed {
  flex: 1 1;
}

.has-radius {
  border-radius: 3px;
}
header .version {
  padding: 2em 0;
  text-align: right;
}
.dice {
  text-align: center;
}

.dice input {
  width: 50%;
  text-align: center;
}

.dice .dice-options,
.dice .dice-result {
  margin-top: 1em;
}

.dice .dice-result  {
  font-size: 2em;
}
.nav {
  max-width: 15vw;
  min-width: 175px;
  margin-right: 2em;
}

.nav h2:first-of-type {
  margin-top: 0;
}

nav,
.dice {
  padding: 1em;
  border: 1px solid #d2d2d2;
  background: whiteSmoke;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li button {
  display: block;
  width: 100%;
  text-align: left;
}

.dice {
  margin-top: 2em;
}
